.burger {
  margin-top: -6px;
  display: block;
  position: relative;
  border: none;
  padding: 0;
  width: 28px;
  height: 12px;
  color: #fff;
  background-color: transparent;
  cursor: pointer;
  z-index: 600;
}
.burger__line {
  margin-top: -4px;
}
.burger::before {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 3px;
  background-color: #fff;
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
.burger::before {
  bottom: 0;
}
.burger__line {
  position: absolute;
  left: 0;
  top: 50%;
  width: 100%;
  height: 3px;
  background-color: currentColor;
  transform: translateY(-50%);
  transition: transform 0.3s ease-in-out;
}
.burger--active {
  margin-top: -8px;
}
.burger--active::before {
  top: 50%;
  transform: rotate(45deg);
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
.burger--active .burger__line {
  margin-top: 0;
  transform: rotate(-45deg);
  transition: transform 0.3s ease-in-out;
}
@media (min-width: 1024px) {
  .burger {
    display: none;
  }
}

